const echartsBarAndLineParseModule = {

  state: {

  },

  mutations: {
    //
    barAndLineParseOptionInit(state, colorList) {

      var defaultChartConfig = JSON.parse(JSON.stringify(state.defaultChartConfig));

      //
      var dataSetList = JSON.parse(JSON.stringify(state.dataSetList));

      //
      var selected_chart_type = defaultChartConfig.select_type;

      //
      var mainTheme = defaultChartConfig.chart_theme.main_theme;

      //
      var colorTheme = defaultChartConfig.chart_theme.color_theme;

      //
      var chart_text = defaultChartConfig.echart_config.chart_text.text;

      //
      var legendDataList = [];

      //
      var legendConfig = defaultChartConfig.echart_config.chart_legend;

      //
      var chartTips = defaultChartConfig.echart_config.chart_tips;

      //
      var chartText = defaultChartConfig.echart_config.chart_text;

      //
      var labelShow = defaultChartConfig.echart_config.chart_label.show;

      //
      var chartGrid = defaultChartConfig.echart_config.chart_grid;

      // X 
      var xAxisItem = defaultChartConfig.analysis_column.data_2;

      // Y 
      var yAxisItem = defaultChartConfig.analysis_column.data_4;

      //  
      var secondAxisItem = defaultChartConfig.analysis_column.data_5;

      //
      var chartDataConversion = defaultChartConfig.echart_config.chart_data_conversion;

      //
      var chartZoom = defaultChartConfig.echart_config.chart_zoom;

      //X
      var xDataList = [];
      
      if(mainTheme === "#fff"){
        
        var fontColor = "#2c2c3d";
        
      }else{
        
        var fontColor = "#fff";
        
      }

      //
      var dataSetListLimit = defaultChartConfig.limit;

      var dataSetLimitNum = parseInt(defaultChartConfig.limit.num);

      if (dataSetListLimit.show === true) {

        if (dataSetList.length >= dataSetLimitNum) {

          if (defaultChartConfig.limit.type === 'before') {

            dataSetList = dataSetList.splice(0, dataSetLimitNum);

          } else {

            dataSetList = dataSetList.splice(dataSetList.length - dataSetLimitNum, dataSetList.length - 1);

          }
        }

      }

      //
      var dataZoomOption = [{
        show: chartZoom.show,
        xAxisIndex: [0],
        bottom: '1%',
        type: 'slider',
        start: 0, //
        end: 100
      }];

      //X
      if (selected_chart_type === 'C210' || selected_chart_type === 'C211' ||
        selected_chart_type === 'C212' || selected_chart_type === 'C240' ||
        selected_chart_type === 'C241' || selected_chart_type === 'C250' ||
        selected_chart_type === 'C280' || selected_chart_type === 'C281' ||
        selected_chart_type === 'C351') {

        var xAxisName = defaultChartConfig.echart_config.chart_x_axis.name;

      } else {

        var xAxisName = '';

      }

      //
      if (selected_chart_type === 'C351') {

        var areaStyleOption = {
          normal: {}
        };

      } else {

        var areaStyleOption = null;

      }

      //X
      var xAxisLabelShow = defaultChartConfig.echart_config.chart_x_axis.show_label;

      //X
      var xAxisSplitLineShow = defaultChartConfig.echart_config.chart_x_axis.show_splitLine;

      //X
      var xAxisLineShow = defaultChartConfig.echart_config.chart_x_axis.show_line;

      //X
      var xAxisLabelRotate = defaultChartConfig.echart_config.chart_x_axis.rotate;

      // X 
      if (window.screen.width <= 1024) {

        xAxisLabelRotate = 45;
      }

      //Y
      var yAxisName = defaultChartConfig.echart_config.chart_y_axis.name;

      //Y
      var yAxisOneLabelShow = defaultChartConfig.echart_config.chart_y_axis.show_label;

      //X
      var yAxisOneSplitLineShow = defaultChartConfig.echart_config.chart_y_axis.show_splitLine;

      //Y
      var yAxisOneLineShow = defaultChartConfig.echart_config.chart_y_axis.show_line;

      //Y
      var yAxisOneLabelRotate = defaultChartConfig.echart_config.chart_y_axis.rotate;

      //
      var yAxisSecond = defaultChartConfig.echart_config.chart_y_secondary_axis;

      //
      var yAxisNameTwo = defaultChartConfig.echart_config.chart_y_secondary_axis.name;

      //
      var yAxisTwoLabelShow = defaultChartConfig.echart_config.chart_y_secondary_axis.show_label;

      //
      var yAxisTwoLineShow = defaultChartConfig.echart_config.chart_y_secondary_axis.show_line;

      //
      var yAxisTwoLabelRotate = defaultChartConfig.echart_config.chart_y_secondary_axis.rotate;

      //(:/)
      var smoothShow = defaultChartConfig.echart_config.chart_smooth;

      //(X/Y)
      var chartMarklineData = defaultChartConfig.echart_config.chart_axis_markline.data;

      //
      var chartEarlyWarningData = defaultChartConfig.echart_config.chart_warning.data;

      //(/)
      var chartDataLabel = defaultChartConfig.echart_config.chart_data_label.data;

      //
      var chartZoomShow = defaultChartConfig.echart_config.chart_zoom.show;

      //
      var seriesDataListOption = [];

      if (smoothShow.type === 'line') {

        smoothShow = false;

      } else {

        smoothShow = true;

      }

      //
      var legendDataOption = {
        icon: 'circle',
        type: 'scroll',
        itemHeight: 10,
        itemWidth: 10,
        show: legendConfig.show,
        left: legendConfig.horizontal,
        top: legendConfig.vertical,
        data: [],
        textStyle:{
          color:fontColor
        }
      };

      //
      var tooltipOption = {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow'
        },
        show: chartTips.show,
        backgroundColor: '#fff',
        padding: 10,
        textStyle: {
          color: fontColor
        },
        borderColor: "rgba(221, 211, 211, 1)",
        borderWidth: 1,
        formatter: function(tooltipList) {

          var tooltipString = '<div class="series-item-title">' + tooltipList[0].name + '</div>';

          for (var index in tooltipList) {

            tooltipString += '<div style="margin-bottom: 5px"></div>' + tooltipList[
              index].marker + tooltipList[index].seriesName + " : " + tooltipList[index].value;

          }

          // if (chartDataConversion.show === true) {

          // }

          return tooltipString;

        }

      };

      //
      var titleOption = {
        show: chartText.show,
        text: chartText.text,
        left: chartText.horizontal,
        top: chartText.vertical,
        textStyle: {
          fontWeight: 'bolder',
          fontSize: chartText.text_styles.font_size,
          color: chartText.text_styles.color
        }
      };

      //X
      var xAxisOption = {
        data: [],
        name: xAxisName,
        axisLine: {
          show: xAxisLineShow
        },
        axisTick: {
          show: xAxisLineShow
        },
        axisLabel: {
          show: xAxisLabelShow,
          rotate: xAxisLabelRotate
        },
        splitLine: {
          show: xAxisSplitLineShow
        }

      };

      if (selected_chart_type === 'C210' || selected_chart_type === 'C215' ||
        selected_chart_type === 'C240' || selected_chart_type === 'C211' ||
        selected_chart_type === 'C241' || selected_chart_type === 'C212') {

        var echartType = 'bar';

        if (selected_chart_type === 'C211' || selected_chart_type === 'C212' || selected_chart_type === 'C241') {

          var stackType = '';

        } else {

          var stackType = null;

        }

      } else if (selected_chart_type === 'C220' || selected_chart_type === 'C351') {

        var echartType = 'line';

        if (selected_chart_type === 'C351') {

          var stackType = '';

        } else {

          var stackType = null;

        }

      } else if (selected_chart_type === 'C250') {

        //()
        if (yAxisSecond.chart_type_one === 'C220') {

          var echartType = 'line';

          var stackType = null;

        } else {

          var echartType = 'bar';

          if (yAxisSecond.chart_type_one === 'C211') {

            var stackType = '';

          } else {


            var stackType = null;

          }

        }

        //()
        if (yAxisSecond.chart_type_two === 'C220') {

          var chartTypeTwo = 'line';

          var stackType = null;

        } else {

          var chartTypeTwo = 'bar';

          if (yAxisSecond.chart_type_two === 'C211') {

            var stackTwoType = '';

          } else {

            var stackTwoType = null;

          }

        }

      }

      //,
      if (selected_chart_type === 'C215' || selected_chart_type === 'C240' || selected_chart_type === 'C241') {

        labelPositon = 'right';

        if (selected_chart_type === 'C215') {

          //  X
          var xAxisOption = [{
            type: 'value',
            name: yAxisName,
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: false,
              rotate: 0
            },
            splitLine: {
              show: false
            }
          }];

          //Y
          var yAxisOption = [{
            data: [],
            name: xAxisName,
            type: 'category',
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              show: yAxisOneLabelShow,
              rotate: 0
            },
            splitLine: {
              show: false
            }

          }];

        } else {

          //  X
          var xAxisOption = [{
            type: 'value',
            name: yAxisName,
            axisLine: {
              show: xAxisLineShow
            },
            axisTick: {
              show: xAxisLineShow
            },
            axisLabel: {
              show: xAxisLabelShow,
              rotate: xAxisLabelRotate
            },
            splitLine: {
              show: xAxisSplitLineShow
            }
          }];


          //Y
          var yAxisOption = [{
            data: [],
            name: xAxisName,
            type: 'category',
            axisLine: {
              show: yAxisOneLineShow
            },
            axisTick: {
              show: yAxisOneLineShow
            },
            axisLabel: {
              show: yAxisOneLabelShow,
              rotate: yAxisOneLabelRotate
            },
            splitLine: {
              show: yAxisOneSplitLineShow
            }

          }];

        }


      } else {

        var labelPositon = 'outside';

        //Y
        var yAxisOption = [{
          silent: false,
          name: xAxisName,
          axisLine: {
            show: yAxisOneLineShow
          },
          axisTick: {
            show: yAxisOneLineShow
          },
          axisLabel: {
            show: yAxisOneLabelShow,
            rotate: yAxisOneLabelRotate
          },
          splitLine: {
            show: yAxisOneSplitLineShow
          }

        }];

      }

      //(X/Y)
      var chartMarklineDataList = [];

      for (var idx in chartMarklineData) {

        var chartMarklineItem = chartMarklineData[idx];

        var chartMarklineName = chartMarklineItem.name;

        var chartMarklineValue = chartMarklineItem.yAxis;

        if (selected_chart_type === 'C215' || selected_chart_type === 'C240' || selected_chart_type === 'C241') {

          var chartMarklineObject = {
            xAxis: chartMarklineValue,
            label: {
              show: true,
              position: 'start',
              textStyle: {
                fontSize: 12
              },
              formatter: chartMarklineName + '{c}'
            },
            lineStyle: {
              width: 2
            }
          }

        } else {

          var chartMarklineObject = {
            yAxis: chartMarklineValue,
            label: {
              show: true,
              position: 'start',
              textStyle: {
                fontSize: 12
              },
              formatter: chartMarklineName + '{c}'

            },
            lineStyle: {
              width: 2
            }
          }

        }

        chartMarklineDataList.push(chartMarklineObject);
      }

      for (var index in yAxisItem) {

        var yAxisItemItem = yAxisItem[index];

        ///
        var markPointData = [];

        //(X)
        legendDataOption.data.push(yAxisItemItem.newName);

        //
        for (var labelIndex in chartDataLabel) {

          chartDataLabelItem = chartDataLabel[labelIndex];

          if (chartDataLabelItem.name === yAxisItemItem.name) {

            markPointData = [{
                type: 'max',
                name: ''
              },
              {
                type: 'min',
                name: ''
              }
            ];

            break;
          }
        }

        var seriesDataItemObject = {
          name: yAxisItemItem.newName,
          type: echartType,
          stack: stackType,
          smooth: smoothShow,
          areaStyle: areaStyleOption,
          label: {
            normal: {
              show: labelShow,
              position: labelPositon,
            }
          },
          itemStyle: {
            label: {
              formatter: function(itemObject) {

                // if (chartDataConversion.show === true) {

                //   itemObject.data = itemObject.data + chartDataSwitchConfig.accurate;

                // }

                return itemObject.data;
              }
            }

          },
          //areaStyle: areaStyle,//
          data: [],
          markLine: {
            silent: true,
            data: chartMarklineDataList
          },
          markPoint: {
            data: markPointData,
            symbolSize: 80
          }
        };

        if (selected_chart_type === 'C215') {

          //
          seriesDataItemObject.barWidth = 12;

        }

        seriesDataListOption.push(seriesDataItemObject);

      }

      //
      if (selected_chart_type === 'C250' && secondAxisItem.length > 0) {

        //
        yAxisOption.push({
          type: 'value',
          name: yAxisNameTwo,
          axisLine: {
            show: yAxisTwoLineShow
          },
          axisTick: {
            show: yAxisTwoLineShow
          },
          axisLabel: {
            show: yAxisTwoLabelShow,
            rotate: yAxisTwoLabelRotate
          }
        });

        for (var idx in secondAxisItem) {

          var valueTwoItem = secondAxisItem[idx];

          legendDataOption.data.push(valueTwoItem.newName);

          seriesDataListOption.push({
            name: valueTwoItem.newName,
            type: chartTypeTwo,
            stack: stackTwoType,
            smooth: smoothShow,
            label: {
              normal: {
                show: labelShow,
                position: labelPositon,
              },
              textStyle: {
                color: fontColor
              },
            },
            yAxisIndex: 1,
            areaStyle: areaStyleOption,
            data: [],
          });
        }
      }

      //
      for (var idx in dataSetList) {

        var dataSetListItem = dataSetList[idx];

        //
        var mulDimensionValue = '';

        // 
        for (var xAxisItemIndex in xAxisItem) {

          var xAxisItemIndexItem = xAxisItem[xAxisItemIndex];

          //
          mulDimensionValue += '-' + dataSetListItem[xAxisItemIndexItem.name];

        }

        if (mulDimensionValue !== '') {

          mulDimensionValue = mulDimensionValue.substring(1);

        }

        if (selected_chart_type === 'C215' || selected_chart_type === 'C240' || selected_chart_type === 'C241') {

          //  Y 
          yAxisOption[0].data.push(mulDimensionValue);

        } else {

          //  X 
          xAxisOption.data.push(mulDimensionValue);

        }

        //  Y 
        for (var yAxisItemIndex in yAxisItem) {

          var valueOneItem = yAxisItem[yAxisItemIndex];

          // if (chartDataConversion.show === true) {

          //   dataSetListItem[valueOneItem.name] = dataSwitchFunction(dataSetListItem[valueOneItem.name],chartDataSwitchConfig);

          // }

          seriesDataListOption[yAxisItemIndex].data.push(dataSetListItem[valueOneItem.name]);

          if (selected_chart_type === 'C215') {

            break;

          }

        }

        // 
        if (selected_chart_type === 'C250' && secondAxisItem.length > 0) {

          for (var secondIndex in secondAxisItem) {

            var valueTwoItem = secondAxisItem[secondIndex];

            var valueOneDataLength = parseInt(yAxisItem.length);

            var valueTwoLocalIndex = valueOneDataLength + parseInt(secondIndex);

            // if (chartDataConversion.show === true) {

            //   dataSetListItem[valueTwoItem.name] = dataSwitchFunction(dataSetListItem[valueTwoItem.name],
            //     chartDataSwitchConfig);

            // }

            seriesDataListOption[valueTwoLocalIndex].data.push(dataSetListItem[valueTwoItem.name]);

          }
        }

      }



      if (selected_chart_type === 'C212') {

        //
        if (seriesDataListOption.length > 0) {

          var regionDataItem = [];

          var baseValue = 0;

          for (var idx in seriesDataListOption[0].data) {

            var fristIndex = parseInt(idx);

            regionDataItem.push(baseValue);

            for (var seriesIndex in seriesDataListOption) {

              var itemSerData = seriesDataListOption[seriesIndex].data;

              baseValue += itemSerData[fristIndex];

            }

          }

          //()
          var auxiliarySeriesData = {
            name: '',
            type: 'bar',
            stack: '',
            itemStyle: {
              normal: {
                barBorderColor: 'rgba(0,0,0,0)',
                color: 'rgba(0,0,0,0)'
              },
              emphasis: {
                barBorderColor: 'rgba(0,0,0,0)',
                color: 'rgba(0,0,0,0)'
              }
            },
            data: regionDataItem
          }

          seriesDataListOption.splice(0, 0, auxiliarySeriesData);

        }

      }

      //
      var echartOption = {
        color: colorList,
        title: titleOption,
        legend: legendDataOption,
        grid: chartGrid,
        dataZoom: dataZoomOption,
        tooltip: tooltipOption,
        xAxis: xAxisOption,
        yAxis: yAxisOption,
        series: seriesDataListOption
      };

      return {
        echartOption: echartOption,
        canChartConfig: {}
      };

    },
  },

  getters: {

  },


}

export default echartsBarAndLineParseModule



