// 2. 
//  "component" 
//  Vue.extend() 
// 

import HomePage from '../components/pages/HomePage.vue'

import LoginPage from '../components/pages/LoginPage.vue'

const routes = [{
    path: '/home',
    name: 'home',
    component: HomePage
  },
  {
    path: '/login',
    name: 'login',
    component: LoginPage
  },
  {
    path: '/',
    component: LoginPage
  }
];

export default routes



